import { INIT_APP_STATE, SET_CUSTOM_USER_PAGE_STYLES, SET_SITE_URL, } from "../constants";
const reducer = (state, action) => {
    switch (action.type) {
        case SET_SITE_URL: {
            const siteURL = action.payload;
            return Object.assign(Object.assign({}, state), { siteURL });
        }
        case INIT_APP_STATE: {
            const { siteURL, publicUserPageId, timezone, isUserAllowedToDelete, userPageCustomStyles, pluginURL, taskUploadsURL, } = action.payload;
            return Object.assign(Object.assign({}, state), { siteURL,
                publicUserPageId,
                timezone,
                isUserAllowedToDelete,
                userPageCustomStyles,
                pluginURL,
                taskUploadsURL });
        }
        case SET_CUSTOM_USER_PAGE_STYLES: {
            const userPageCustomStyles = action.payload;
            return Object.assign(Object.assign({}, state), { userPageCustomStyles });
        }
        default:
            return state;
    }
};
export { reducer };
